// pages/admin/messages/index.js
import { useEffect, useState } from "react";
import Link from "next/link";
import api from "../../../utils/api";

export default function AdminMessages() {
  const [users, setUsers] = useState([]);

  useEffect(() => {
   const fetchUsers = async () => {
     try {
        const res = await api.get("/messages/users");
        console.log("API response:", res.data);
      } catch (err) {
        console.error(err);
      }
    };
    fetchUsers();
  }, []);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await api.get("/messages/users");
        console.log("Users fetched:", res.data); // debug log
        setUsers(Array.isArray(res.data) ? res.data : []);
      } catch (err) {
        console.error(err);
        setUsers([]);
      }
    };
    fetchUsers();
  }, []);

  return (
    <div style={{ padding: "20px" }}>
      <h2>All Users Who Messaged Admin</h2>
      {users.length === 0 ? (
        <p>No users have sent messages yet.</p>
      ) : (
        <ul>
          {users.map((u) => (
            <li key={u._id} style={{ marginBottom: "10px" }}>
              <Link href={`/admin/messages/${u._id}`}>
                <a style={{ color: "blue", textDecoration: "underline" }}>
                  {u.name || u.email || "Unnamed User"}
                </a>
              </Link>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
